/* FormGender.module.css */

.loginForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    width: 300px;
    margin: 0 auto;
    background-color: #f9f9f9;
  }
  
  .loginForm h4 {
    margin-bottom: 10px;
    color: #333;
  }
  
  .loginForm input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .loginForm button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .loginForm button:hover {
    background-color: #0056b3;
  }
  
  .errors {
    color: red;
    font-size: 12px;
    margin-top: -10px;
    margin-bottom: 10px;
  }